.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 600px; */
  height: 100vh;
  width: 100vw;
  background-color: #e74c3c;
}

.current-image {
  border-radius: 0.5rem;
  box-shadow: 0px 0px 7px #666;
  width: 45em;
  height: 30em;
  object-fit: cover;
}

.arrow {
  position: relative;
  width: 2rem;
  height: 2rem;
  color: #555;
  filter: drop-shadow(0px 0px 5px #fff);
  cursor: pointer;
}

.arrow-left {
  left: 4rem;
}

.arrow-right {
  right: 4rem;
}

.circle-indicators {
  display: flex;
  position: absolute;
  bottom: 1rem;
}

.current-indicator {
  background-color: #fff;
  height: 15px;
  width: 15px;
  border-radius: 50px;
  border: none;
  outline: none;
  margin: 0 0.2rem;
  cursor: pointer;
  border: 2px solid black;
}

.hide-current-image {
  display: none;

}

.inactive-indicator {
  background-color: black;
  border: 2px solid #fff;
}

.loading,
.error {
  height: 100vh;
  
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  color: black;
  font-size: 4rem;

}